/*       */
var delayedResolve = require('./delayed-resolve');
/**
 */

/**
 * Asynchronous API for checking state of the promise. The returned promise is fulfilled as soon as
 * possible.
 *
 * Note: there is no public synchronous API for this.
 * @param promise The promise to determine the state of.
 * @example
 */


function state(promise) {
  return new Promise(function (resolve) {
    promise.then(function (value) {
      return resolve({
        name: 'fulfilled',
        value: value
      });
    }, function (reason) {
      return resolve({
        name: 'rejected',
        reason: reason
      });
    });
    delayedResolve({
      name: 'pending'
    }).then(resolve);
  });
}

module.exports = state;